function Main(props){
    return(
        <a href="#">
            <div className="imgBox">
                <img src={`/images/main_slide${props.i}.jpg`} alt="0" />
            </div>
            <div className="txtBox">
                <p>{props.m[props.i].d}</p>
                <p>{props.m[props.i].t}</p>
            </div>
        </a>
    )
}
function Slist(props){
    return(
        <a href="#">
            <div className="imgB">
            <img src={`/images/cont1_${props.i}.jpg`} alt="1" />
            </div>
            <div className="txtB">
            <p>{props.sec1[props.i].t}</p>
            <p>{props.sec1[props.i].d}</p>
            <p>{props.sec1[props.i].p}</p>
            </div>
        </a>
    )
}
function Section2(){
    return(
        <section id="section2">
          <div className="imgB">
            <img src="/images/cont2.jpg" alt="" />
          </div>
          <div className="txtB">
            <h2> </h2>
            <p>     <span> </span></p>
            <span></span>
          </div>
        </section>
    )
}
function Slide3(props){
    return(
        <li>
            <a href="#">
            <div className="imgB"><img src={`/images/cont3_${props.i}.png`} alt="0" /></div>
            <div className="txtB">
                <p>{props.sec3[props.i].t}</p>
                <p>{props.sec3[props.i].d}</p>
                <p>{props.sec3[props.i].p}</p>
            </div>
            </a>
        </li>
    )
}
function Section4({sec4}){
    return(
        <section id="section4">
          <div className="list">
            <div className="imgB">
              <img src="/images/cont4_0.jpg" alt="0" />
            </div>
            <div className="txtB">
              <h3>{sec4[0]}</h3>
              <span> </span>
            </div>
          </div>
          <div className="list">
            <div className="imgB">
              <img src="/images/cont4_1.jpg" alt="0" />
            </div>
            <div className="txtB">
              <h3>{sec4[1]}</h3>
              <span> </span>
            </div>
          </div>
        </section>
    )
}
function Fdown(){
    return(
        <div className="fdown">
            <div className="footIn">
                <div className="tel">
                <p>() : 080-023-5454 (-: AM 09:00 ~ PM 06:00)</p>
                </div>
                <div className="copy">
                <p>&copy; AMOREPACIFIC COPERATION. All right reserved.</p>
                </div>
            </div>
        </div>
    )
}
// export default Main;
export {Main, Slist, Section2, Slide3, Section4, Fdown}